<template>
  <table class='table table-striped table-hover'>
            <thead>
                <tr>
                    <th>Nombre</th>
                    <th>Monto</th>
                    <th>Ver</th>
                </tr>     
            </thead>
            <tbody>

                <tr v-for="fac in getCuentasAll" :key="fac._id" >
                    <td>{{fac.nombre}}</td>
                    <td>{{fac.monto}}</td> 
                    <td>
                        <button 
                            class='btn btn-info'
                            @click="getFacturas(fac.id)"
                            :disabled="getIsLoading"
                        >
                            Ver
                        </button>
                       
                    </td>                       
                </tr>
                        


            </tbody>
            
        </table>
      
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
    computed:{
        ...mapGetters('cuenta',['getCuentasAll', 'getIsLoading'])
    },
    methods:{

        ...mapActions('cuenta',['getCuentas', 'getFacturas'])
    },
    created(){
        this.getCuentas()
    }
}
</script>

